// === Template colors
$color_brand: #7b38ff;
$color_secondary: #10173e;
$color_dark: #060b27;
$color_black: #080134;
$color_text: #9c99ae;
$color_light: #ffffff21;
$color_green: #01c5ba;
$color_blue: #72e6ff;
$color_icon_green: #064462;
$color_icon_red: #561857;
$color_red: #ff6871;
$color_pink: #ff72d2;
$color_yellow: #ffd166;
$color_white: #ffffff;
$color_border: rgba(255, 255, 255, 0.29);
$color_border2: linear-gradient(
  to right,
  rgba(255, 255, 255, 0.5) 0%,
  #00000000 50%,
  rgba(255, 255, 255, 0.5) 100%
);
$color_stroke: linear-gradient(
  180deg,
  rgba(255, 255, 255, 0.19) 0%,
  rgba(255, 255, 255, 0) 100%
);
$color_grad: linear-gradient(
  rgba(255, 255, 255, 1) 0%,
  rgba(255, 255, 255, 0) 100%
);

